import styled from '@emotion/styled';
import { Tab } from '@material-ui/core';
import TabContext from '@material-ui/lab/TabContext/TabContext';
import TabList from "@material-ui/lab/TabList/TabList";
import TabPanel from '@material-ui/lab/TabPanel/TabPanel';
import DelayedLoader from 'components/DelayedLoader';
import { css } from 'emotion';
import React, { useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";

const BusinessDetails = React.lazy(() => import('./BusinessDetails'))
const Users = React.lazy(() => import('./Users'))
const Facility = React.lazy(() => import('./Facility'))

const TabListStyle = css`
  .MuiTabs-indicator {
    height: 4px;
  }
  .MuiTab-root {
    padding: 6px 20px;
    &.Mui-selected {
      background-color: rgba(0, 0, 0, 0.08);
    }
  }
`;

const AccountName = styled.div`
  font-size: 18px;
  padding: 16px;
`;

const TabPanelContainer = styled.div`
`;

const TabListContainer = styled.div`
  position: relative;

  :before {
    content: '';
    position: absolute;
    top: 47px;
    left: 0;
    right: 0;
    border-bottom: 1px solid rgba(0,0,0,0.12);
  }
`;

function Account() {
  const params = useParams();
  const history = useHistory();
  const tabFromRoute: string = (params as any).tab;
  const accountFromRoute: string = (params as any).accountId;
  const [activeTab, setActiveTab] = React.useState<string>(tabFromRoute);

  // Tab Route Change
  useEffect(
    () => {
      setActiveTab(tabFromRoute);
    },
    [tabFromRoute, setActiveTab],
  );

  return (
    <React.Fragment>
      <AccountName>Breaking Bad - Invoice Finance</AccountName>
      <TabListContainer>
        <TabContext value={activeTab}>
          <TabList
            className={TabListStyle}
            variant="scrollable"
            scrollButtons="auto"
            indicatorColor="primary"
            onChange={(evt, newTab) => {
              history.push(`/accounts/${accountFromRoute}/${newTab}`);
            }}
          >
            <Tab label="Business" value="business" />
            {/* <Tab label="Corporate Structure" value="structure" /> */}
            {/* <Tab label="Financials" value="financials" /> */}
            <Tab label="Facility" value="facility" />
            <Tab label="Customers" value="customers" />
            <Tab label="Users" value="users" />
            <Tab label="Xero" value="xero" />
            <Tab label="Requests" value="requests" />
          </TabList>
          <TabPanelContainer>
            <TabPanel value="business"><React.Suspense fallback={<DelayedLoader />}><BusinessDetails /></React.Suspense></TabPanel>
            <TabPanel value="structure"><React.Suspense fallback={<DelayedLoader />}>Corporate Structure</React.Suspense></TabPanel>
            <TabPanel value="financials"><React.Suspense fallback={<DelayedLoader />}>Financials</React.Suspense></TabPanel>
            <TabPanel value="facility"><React.Suspense fallback={<DelayedLoader />}><Facility /></React.Suspense></TabPanel>
            <TabPanel value="customers"><React.Suspense fallback={<DelayedLoader />}>Customers</React.Suspense></TabPanel>
            <TabPanel value="users"><React.Suspense fallback={<DelayedLoader />}><Users /></React.Suspense></TabPanel>
            <TabPanel value="xero"><React.Suspense fallback={<DelayedLoader />}>Xero</React.Suspense></TabPanel>
            <TabPanel value="requests"><React.Suspense fallback={<DelayedLoader />}>Requests</React.Suspense></TabPanel>
          </TabPanelContainer>
        </TabContext>
      </TabListContainer>
    </React.Fragment>
  )
}

export default Account;
